.body-box {
    min-height: 100vh;
    background-color: #F0F0F0;
}

.k-d-cont-box {
    width: 95%;
    margin: 0 auto;
    padding-bottom:10vh; 
}

.k-d-ico {
    width: 40px;
    height: 40px;
}

/*  */
.k-d-big-box {
    background-color: #fff;
    margin-top: 2.6vw;
    margin-bottom: 5.468vw;
    padding-bottom: 9.63vw;
}

.k-d-box {
    display: flex;
    font-size: 24px;
    font-weight: bold;
    color: rgba(51, 51, 51, 1);
    padding-top: 3.77vw;
    padding-bottom: 3.77vw;
    border-bottom: 1px solid rgba(112, 112, 112, 0.2);
}

.k-d-box2 {
    display: flex;
    font-size: 22px;
    color: rgba(102, 102, 102, 1);
    padding-top: 2vw;
    padding-bottom: 2vw;
}

.k-d-xian {
    width: 95%;
    border-bottom: 1px solid rgba(112, 112, 112, 0.1);
    margin: 0 auto;
}

.k-d-child1 {
    flex: 1;
    padding-left: 2.86vw;
}

.k-d-child2 {
    flex: 1;
    text-align: center;
}

.k-d-child3 {
    flex: 1;
    text-align: right;
    padding-right: 2.86vw;
}

/*  */
.k-d-btn {
    border: 1px solid #DEC08E;
    background-color: #DEC08E;
    color: #fff;
    font-size: 24px;
    color: rgba(255, 255, 255, 1);
    height: 7.8vw;
    line-height: 7.8vw;
}
.k-d-btn2 {
    border: 1px solid #fff;
    background-color: #fff;
}