/* 桌号 */
.k-big-box {
    display: flex;
    flex-wrap: wrap;
    padding: 0 .8vw;
    padding-bottom:10vh; 
}

.k-box {
    width: 30vw;
    border: 1px solid #DEC08E;
    text-align: center;
    margin-bottom: 2vw;
    border-radius: 1.3vw;
    float: left;
    margin-left: 2vw;
    position: relative;
    overflow: hidden;
}

.k-box.active>.k-box-img {
    display: block;
}
.k-box.active>.k-box-img2 {
    display: block;
}

.k-box-img {
    display: none;
    width: 30vw;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
}
.k-box-img2 {
    display: none;
    width: 9vw;
    height:9vw;
    position: absolute;
    right: 5px;
    bottom: 3px;
}

.k-title {
    font-size: 38px;
    color: rgb(222, 192, 142);
    padding: 2vw 0 3.8vw 0;
}

.k-text1 {
    font-size: 22px;
    color: rgb(153, 153, 153);
}

.k-text2 {
    font-size: 16px;
    color: rgb(153, 153, 153);
    padding: 0.8vw 0 2.3vw 0;
}

/* 桌号end */